.infoTooltip {
  box-sizing: border-box;
  background-color: #fff;
  width: 430px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 0 0;
  margin: 0;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
}

@media screen and (min-width: 320px) and (max-width: 424px) {
  .infoTooltip {
    width: 280px;
  }
}
